
import LikeBtn from './LikeBtn.vue'
import { defineComponent, ref } from 'vue'
export default defineComponent({
  name: 'LikesComponent',
  components: { LikeBtn },
  props: {
    likes: {
      type: Number,
      required: true
    },
    likeBtnClickHandler: {
      type: Function,
      required: true
    },
    isLiked: {
      type: Boolean,
      required: true
    }
  },
  setup () {
    const showLikes = ref(false)

    return {
      showLikes
    }
  }
})
